import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class TextFieldLabel extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			disabled,
			fixed,
			htmlFor,
			onTouchTap,
			className,
			children
		} = props;

		const klassName = KlassName('field-label',{
			['disabled']:('disabled' in props && disabled !== false ),
			['field-label--fixed']:('fixed' in props && fixed !== false ),
		},className);

		return (
			<label
				className={klassName}
				htmlFor={htmlFor}
				onTouchTap={onTouchTap}
			>
				{children}
			</label>
		);
	}
};

TextFieldLabel.propTypes = {
	disabled: PropTypes.bool,
	fixed: PropTypes.bool,

	className: PropTypes.string,
	children: PropTypes.node,
	htmlFor: PropTypes.string,

    onTouchTap: PropTypes.func,
};

TextFieldLabel.defaultProps = {
	disabled: false,
	fixed: false,
};

export default TextFieldLabel;